
@font-face {
  font-family: 'SourceHanSansCN-Regular';
  src: url(../../static/fonts/SourceHanSansCN-Regular.1335002.ttf) format('truetype');
}
#app {
  width: 100%;
  height: 100vh;
  /* min-width: 1200px; */
}
/*
初始化css
*/
* {
  margin:0; 
  -webkit-overflow-scrolling: touch;
  padding:0;
}
body {
  -ms-touch-action: none;
      touch-action: none;
  /* font-size: 15px; */
  font-family: "SourceHanSansCN-Regular";   
  font-weight: 500;
  color: #000000;
  overflow:scroll;
  overflow-y:hidden;
  overflow-x:hidden;
  /* min-width: 1200px; */
}
a {
  text-decoration:none;
  resize: none;
  outline: none;
  color: #000;
}
em {
  font-style:normal;
}
li{
  list-style:none;
}
img{
  border:0; vertical-align:middle;
}
table{
  border-collapse:collapse;
  border-spacing:0;
}
p {
  word-wrap:break-word;
}
input {
  outline: none;
  resize: none;
  border: none 0;
}
button {
  border: none;
  outline: none;
  cursor:pointer;
}
/* 每个页面的页面样式 */
.page {
  font-family: "SourceHanSansCN-Regular";
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
.loading-container {
  position: fixed;
  z-index: 99999;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.sidebar {
  -webkit-transition: opacity .75s ease;
  transition: opacity .75s ease;
}
/* 下面是公共的样式 */
@media only screen and (min-width: 740px) {
.bg-bulb {
    position: absolute;
    width: 3.65rem;
    height: 3.65rem;
}
.turn-page {
    position: relative;
    margin-top: 0.35rem;
    width: 100%;
}
.turn-page>img {
    float: left;
    display: block;
    width: 0.68rem;
    height: 0.76rem;
}
.turn-page>span {
    float: left;
    display: block;
    margin-top: 0.58rem;
    font-size: 0.18rem;
    line-height: 0.18rem;
    color: #4a4a4a;
}
}
@media only screen and (max-width: 740px) {
.bg-bulb {
    position: absolute;
    width: 3.65rem;
    height: 3.65rem;
}
.turn-page>img {
    display: block;
    float: left;
    width: 0.75rem;
    height: 0.75rem;
}
.turn-page>span {
    display: block;
    float: left;
    font-size: 0.26rem;
    line-height: 0.26rem;
    margin-top: 0.5rem;
    margin-left: 0.16rem;
    color: #4d4545;
}
section {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
}
.turn-page {
  margin-top: 0.46rem;
}
.turn-page::after {
  content: "";
  display: block;
  clear: both;
}
.bulb {
  -webkit-transition: opacity .75s ease;
  transition: opacity .75s ease;
}
.fade-out {
  opacity: 0!important;
  /* z-index: -9; */
}
.fade-in {
  opacity: 1!important;
  /* z-index: 1; */
}

